<style>
.sortBtn,
.sortBtn img {
  cursor: pointer;
  transition: 0.3s;
}

.sortBtn:hover img {
  transform: scale(1.1);
}


.workListWrap {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
    transition: .3s;
}

.w50{
    width: 50%;
    padding-left:15px;
    padding-right:15px;
   
}

@media screen and (max-width:600px){
  .mobile-mt-2{
    margin-top: 2rem;
  }

  .w50{
    width:100%
  }

  .columnBtn{
    display: none;
  }
}
 
</style>
