        Tag: ["Html5", "CSS3", "RWD", "JavaScript", "JQuery", "C#", "M-SQL"],
        text: "大學眼鏡電商後台",
        img: "homework8",
        date: "2019.06",
        descript:
          "因公司增設轉單的商品可販賣，因此電商部門提出後台的需求，需要進行產品的上下架管理、庫存管控、以及出貨、發票的開立...等等，需多邏輯的複雜度也相當高，但也藉這個機會學習如何使用C#撰寫後端語言。",
        skill: [
          "前端框架套件整合",
          "撰寫C#後端API",
          "Datatable使用",
