      {
        row: 8,
        name: "SurfWaveDemo",
        Tag: [
          "Html5",
          "CSS3",
          "RWD",
          "JavaScript",
          "JQuery",
          "JQuery Pulgin",
          "PHP",
          "MYSQL",
        ],
        text: "衝浪品牌電商",
        img: "homework2",
        date: "2019.02",
        descript:
          "衝浪電商網站是針對衝浪愛好者所設計的一個功能型電商網站，裡面除了商品販賣、衝浪課程、還包刮了衝浪地圖可查閱，讓衝浪的浪友們能透過即時的天氣預報去追逐屬於自己的那一片浪。",
        skill: [
          "RWD響應式網頁",
          "JQuery套件使用",
          "PHP後端API撰寫",
          "氣象局API串接",
          "MYSQL資料庫使用",
        ],
        url: "http://demowork.42web.io/",
      },
      {
        row: 9,
        name: "HotalWebDemo",
        Tag: ["Html5", "CSS3", "RWD", "JavaScript", "JQuery", "JQuery Pulgin"],
        text: "飯店官網",
        img: "homework1",
        date: "2018.09",
        descript:
          "這是我在資策會受訓中第一個個人作品，也是我踏進網頁領域中的第一份作品，從設計、呈現、切版，都是第一次自己獨力完成，花費了很多時間去思考，但完成作品後覺得非常有成就感，未來有時間希望能進一步改寫成SPA網頁",
        skill: ["RWD響應式網頁", "JQuery套件使用", "CSS動畫"],
        url: "https://yixianghong.github.io/mywork3/index.html",
      },
    ]);
    //近來這頁執行資料更改
    let ChangeData = function() {
      let projectName = route.params.id;
      thisPageData.value = workList.filter((x) => {
        return x.name == projectName;
      });
    };

    let ChangePreViewData = function() {
      let projectName = route.params.id;

      //找我現在是第幾筆資料
      let myRow = workList.find(function(item, index, array) {
        return item.name === projectName;
      }).row;

      //找下一頁資料
      let nextData = workList.find((x) => {
        let next = myRow + 1;

        if (next > workList.length) {
          next = 1;
        }
        return x.row == next;
      });

      //找上一頁資料
      let prevData = workList.find((x) => {
        let prev = myRow - 1;
        if (prev == 0) {
          prev = workList.length;
        }
        return x.row == prev;
      });

      preViewData.length = 0;
      preViewData.push(prevData, nextData);
    };

    ChangeData();
    ChangePreViewData();

    const location = (value) => {
      router.push({ path: `/MyWork/${value}` }).then(() => {
        ChangeData();
        ChangePreViewData();
      });
    };

    onMounted(() => {});
    return {
      workList,
      thisPageData,
      preViewData,
      location,
    };
  },
};
</script>
